/* Reset styles */
body,
h1,
h2,
p,
ul,
li,
a {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Arial', sans-serif;
  line-height: 1.6;
  background-color: #f9f9f9;
  color: #333;
}

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Page section styling */
.page {
  max-width: 1200px;
  width: 90%;
  margin: 2em 0;
  background: white;
  padding: 2em;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

/* Banner styling */
.banner {
  background: url('/src/assets/banner.jpg') no-repeat center center/cover;
  height: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  color: white;
  font-size: 2.5em;
  font-weight: bold;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.logo {
  margin-top: 1em;
  max-width: 200px;
  height: auto;
}

/* Image styling */
img {
  max-width: 100%;
  border-radius: 8px;
}

/* Map styling */
.map {
  width: 100%;
  height: 300px;
  border: none;
  border-radius: 8px;
}

/* Responsive design */
@media (max-width: 768px) {
  .banner {
    font-size: 1.8em;
    height: 200px;
  }

  .page {
    padding: 1.5em;
  }
}