.about-us {
  width: 100%;
  padding: 50px 20px;
  text-align: center;
  background: url('../assets/images/bg.jpeg') no-repeat center center/cover;
  color: white;
  /* ใช้สีตัวอักษรที่เหมาะสมกับพื้นหลัง */
  background: linear-gradient(135deg, #00274d, #0056b3);
}

.about-us h2 {
  font-size: 2.5rem;
  margin-bottom: 20px;
  color: white;
  /* สีขาวเพื่อความชัดเจน */
}

.about-us p {
  max-width: 800px;
  margin: 0 auto 40px;
  font-size: 1.2rem;
  color: #f0f0f0;
  /* สีอักษรที่อ่านง่าย */
}

.sections {
  display: flex;
  justify-content: center;
  gap: 20px;
}


.section {
  background: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  flex: 1;
  max-width: 300px;
}

.section:hover {
  transform: translateY(-5px);
  transition: all 0.3s;
}